.toggle {
  text-align: center;
}

.context {
  float: left;
  padding-left: 7px;
  font-size: 1.1rem;
  font-weight: 400;
}

.sound-state {
  padding-left: 4px;
}

.toggle-switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.toggle-switch .input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #fff;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.input:checked + .slider {
  background-color: #319612;
}

.input:focus + .slider {
  box-shadow: 0 0 1px #31b975;
}

.input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
@media only screen and (max-width: 600px) {
  .toggle {
    margin-top: 1.8rem;
    text-align: left;
  }
  .context {
    display: none;
  }
  .sound-state {
    display: none;
  }
  .slider {
    width: 100%;
  }
}
